.homeWrapper{
    background-image: linear-gradient( #0f9b0f, #52c234, #52c234, #0f9b0f);
    height: 95vh;
    margin-top: 0;
    padding-top: 0;
}
h1{
    margin-top: 0;
    padding-top: 0; 
}

.homeWrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.homeText{
    color: #FFF;
    text-shadow: 5px 5px 10px black;
    margin-bottom: 10px;
    margin-top: 10px;
}
.homeLogo{
    width: 50%;
    height: auto;
    border-radius: 50px;
    border: 1px white solid;
    box-shadow: 5px 10px 20px;
}